import React from "react";
import Navbar from "./components/Navbar";
import "./App.css";
import Home from "./components/pages/Home";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  HashRouter,
} from "react-router-dom";
import WebDeveloper from "./components/pages/WebDeveloper";
import Projects from "./components/pages/Projects";
import Podcasts from "./components/pages/Podcasts";
import Contact from "./components/pages/Contact";
import ReactNativeProject from "./components/pages/ReactNative";

function App() {
  return (
    <>
      <HashRouter>
        <Navbar />
        <Switch>
          <Route path="/" exact component={Home} />
          <Route path="/webdeveloper" component={WebDeveloper} />
          <Route path="/projects" component={Projects} />
          <Route path="/reactnative" component={ReactNativeProject} />
          <Route path="/podcasts" component={Podcasts} />
          <Route path="/contact" component={Contact} />
          <Route component={Home} />
        </Switch>
      </HashRouter>
    </>
  );
}

export default App;
